.works {
  padding: 0 3rem 0 3rem;
  height: 100vh;
  align-items: center;
  position: relative;
  margin-bottom: 2rem;
}

.skils {
  color: var(--orange);
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
}



.swiper {
  width: 100%;
  height: 70%;
  border: 7px solid var(--orangeCard);
  box-shadow: var(--boxShadow);
  border-radius: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* padding-left: 1rem; */
}

.s-blur2 {
  left: 40rem;
  top: 4rem;
}

.s-blur1 {
  top: 13rem;
  left: -18rem;
}



.skills-div{
  width: 650px;
  height: 300px;
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 6rem;
  margin-left: 10rem;
  
}



.swiper-container {
  width: 480px;
}

@media screen and (max-width: 320px) {
 .skills-div{
  grid-template-columns: repeat(1,1fr);
 }
 .skills-img{
  transform: scale(0.3);
 }
}

@media screen and (max-width: 700px) {
  .skills-div{
    width: 650px;
  height: 300px;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 4rem;
  justify-content: center;
  align-items: center;
  }

  .skills-img{
  transform: scale(0.6);
}
}