.portfolio {
  padding: 0 3rem 0 6rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  margin-bottom: 2rem;

}
/* heading */
.portfolio span:nth-of-type(1) {
  color: var(--black);
  font-size: 2rem;
  font-weight: bold;
}

.portfolio span:nth-of-type(2) {
  color: var(--orange);
  font-size: 2.5rem;
  font-weight: bold;
}

/* slider */
.portfolio .swiper{
    overflow: visible!important;
}

.portfolio-slider {
  margin-top: 3rem;
  width: 100%;
}

.portfolio-slider .swiper-slide {
  width: 20rem;
  position: relative;
}





.portfolio-contener{
  width: 80vw;
  height: 100%;
  border: 7px solid var(--orangeCard);
  box-shadow: var(--boxShadow);
  border-radius: 20px;
  display:flex;
  justify-content: center;
  align-items: center;
}

.portfolio-div{
  display: grid;
  grid-template-columns: repeat(2,1fr);
  width: 100%;
  height: 100%;
  gap: 15px;
}

.portfolio-div img{
  border-radius: 5px;
  height: 300px;
}
.about{
  border-radius: 5px;
}

.about>:nth-child(1){
  font-weight: bold;
  font-size:larger;
}
.about>:nth-child(2){
  font-size: 14px;
  text-align: center;
}
.about>:nth-child(3){
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-top: 10px;
}


.tech-stack-title{
  text-decoration-line: underline;
  font-weight: bold;
  font-size: 20px;
}

.tech-stack{
  color: var(--orange);
  margin-top: -1rem;
}




.primary-btn {
  display: inline-block;
  font-size: 15px;
  font-family: "Play", sans-serif;
  font-weight: 700;
  padding: 14px 32px 12px;
  color: black;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  position: relative;
  z-index: 1;
}

.primary-btn:hover:before {
  height: 100%;
  width: 100%;
}

.primary-btn:hover:after {
  height: 100%;
  width: 100%;
}

.primary-btn:before {
  position: absolute;
  left: 0;
  top: 0;
  height: 30px;
  width: 30px;
  border-left: 2px solid #00bfe7;
  border-top: 2px solid #00bfe7;
  content: "";
  z-index: -1;
  -webkit-transition: all, 0.7s;
  -o-transition: all, 0.7s;
  transition: all, 0.7s;
}

.primary-btn:after {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 30px;
  width: 30px;
  border-right: 2px solid #00bfe7;
  border-bottom: 2px solid #00bfe7;
  content: "";
  z-index: -1;
  -webkit-transition: all, 0.7s;
  -o-transition: all, 0.7s;
  transition: all, 0.7s;
}


